
  
  export default {
    name: 'l-modal',
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      width: {
        type: String,
        default: '50%',
      },
      top: {
        type: String,
        default: '100px'
      },
      ok: {
        type: Function,
      },
      cancel: {
        type: Function
      },
      title: {
        type: String,
        default: ''
      },
      content: {
        type: String,
        default: ''
      }
    },
    setup(props, context) {
      const styles = {
        width: props.width,
        marginTop: props.top
      }
      const close = () => {
        context.emit('update:visible', false)
      }
  
      const cancel = () => {
        if (props.cancel && props.cancel() !== false)
          close()
      }
  
      const ok = () => {
        if (props.ok && props.ok() !== false)
          close()
      }
      
      return {
        styles,
        cancel,
        ok,
        close
      }
    }
  }
  