
  import {
    reactive,
    toRefs
  } from 'vue'
  export default {
    name: 'LAccordion',
    props: {
      items: {
        required: true,
        type: Array,
        default: function () {
          return [{
            title: "JavaScript",
            content: "这是内容",
            show: true
          }]
        }  
      },
      itemWidth: {
        type: Number,
        default: function () {
          return 600
        }   
      },
      activeColor: {
        type: String,
        default: function () {
          return '#000'
        }   
      },
      fontSize: {
        type: Number,
        default: function () {
          return 14
        }   
      }
    },
    setup() {
      //定义接口类型.
      interface ItemObj {
        title: string;
        content: string;
        show: boolean;
      }
      const data = reactive({
        active: 0,
        changeItem: (vo: ItemObj, inx: number) => {
          if (inx === data.active) {
            vo.show = !vo.show
          } else {
            vo.show = true
          }
          data.active = inx
        }
      })
      return {
        ...toRefs(data)
      }
    }
  }
